* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.nav-list {
  flex-direction: column;
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav-list-link {
  color: #373163;
  text-align: center;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
  position: relative;
}

.nav-list-link:before {
  content: "";
  width: 0;
  height: 1.5px;
  background-color: #373163;
  transition: width .5s;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.nav-list-link:hover:before {
  width: 100%;
}

